import React, { useState } from "react";
import NavBar from "../../components/NavBar/NavBar";
import Footer from "../../components/Footer/Footer";
import { Chart as ChartJS, ArcElement, Tooltip, Legend } from "chart.js";
import { Doughnut } from "react-chartjs-2";

ChartJS.register(ArcElement, Tooltip, Legend);

const MyBonus = () => {
    const data = {
        labels: [
            'Red',
            'Blue',
            'Yellow',
            'Orange'
        ],
        datasets: [{
            label: 'Total de ganancias',
            data: [300, 50, 100, 48],
            backgroundColor: [
                'rgb(255, 99, 132)',
                'rgb(54, 162, 235)',
                'rgb(255, 205, 86)',
                'rgb(255, 105, 75)'
            ],
            hoverOffset: 4
        }],

    };

    return <>
        <NavBar />
        <div className="h-full  text-center bg-gray-100 pt-10 pb-20 grid grid-cols-1 md:grid-cols-2 gap-4 px-5" >
            <div className="flex justify-start items-center  bg-white rounded-lg shadow-lg">
                <div className="relative  p-8">

                    <Doughnut data={data}
                        options={{
                            plugins: {
                                title: {
                                    display: true,
                                    text: "Users Gained between 2016-2020"
                                },
                                legend: {
                                    display: false,
                                }
                            }
                        }}

                    />
                     <p className="absolute top-1/2 left-1/2 -translate-x-1/2 -translate-y-1/2 text-4xl font-bold">18k</p>
                </div>
                <div>
                    <p></p>
                </div>
            </div>
            <div className="history bg-red-500">
                <h1 className="text-3xl text-start">Historial</h1>
            </div>
        </div>
        <Footer />
    </>
}





export default MyBonus;