import React, { useEffect, useRef, useState } from "react"
import './css/index.css';
import NavBar from "../../components/NavBar/NavBar";
import Footer from "../../components/Footer/Footer";
import { useParams } from "react-router-dom";
import redsysImage from "../../assets/redsysImage.png"
import PrimaryButton from "../../components/Buttons/PrimaryButton";
import { getTotalToPay } from "../../api/enrollments";
import useUser from "../../hooks/auth/useUser";
import { ToastContainer, toast } from "react-toastify";
import LoadingIcon from "../../components/Loading/Icon";
import { processPayment } from "../../api/redsys";
import Loading from "../../components/Loading/Loading";
import ModalAccept from "../../components/Modal/ModalAccept";
import TextFieldLabel from "../../components/TextField/TextFieldLabel";

const PayEnrollment = () => {
    const { enrollmentId } = useParams();
    const [userData] = useUser();
    const [amount, setAmount] = useState(undefined);
    const [stateLoading, setStateLoading] = useState(false);
    const [modalAccept, setModalAccept] = useState(false);
    const formRef = useRef(null)
    const toastConfig = {
        position: "top-right",
        autoClose: 2500,
        hideProgressBar: false,
        closeOnClick: true,
        pauseOnHover: true,
        draggable: true,
        progress: undefined,
    };

    const handleProcessPayment = e => {
        e.preventDefault();
        try {
            setModalAccept({
                ok: () => {
                    const formValues = Object.fromEntries(new FormData(e.target).entries());
                    setStateLoading(true);
                    setModalAccept(false);
                    const body = {
                        card_number: formValues.card_number,
                        expiration_date: formValues.expiration_date,
                        cvv: formValues.cvv,
                    };
                    console.log(body);
                    processPayment(body, enrollmentId, userData.token, (json, error) => {
                        if (json) {
                            console.log(json);
                            if (json.code == 'ok') {
                                toast.success(json.message);
                            } else {
                                toast.error('Ha ocurrido un error a la hora de procesar el pago')
                            }
                        } else {
                            toast.error('Ha ocurrido un error a la hora de procesar el pago')
                        }
                        setStateLoading(false)
                    });
                },
                cancel: () => setModalAccept(false),
            });


        } catch (error) {
            toast.error('Ha ocurrido un error a la hora de procesar el pago')
            setStateLoading(false)
        }

    }

    useEffect(() => {
        setAmount(undefined)
        formRef.current.reset();
        getTotalToPay(enrollmentId, userData.token, (json, error) => {
            if (json) {
                if (json.code === 'ok') {
                    setAmount(json.data)
                } else {
                    toast.error(json.message, toastConfig)
                }
            } else {
                toast.error('Ha ocurrido un error en el momento de obtener los datos', toastConfig)
            }
        });
    }, [enrollmentId]);
    return (<div className="h-screen flex flex-col">
        <NavBar />
        {modalAccept && <ModalAccept {...modalAccept} />}
        {stateLoading && <Loading />}
        <ToastContainer />
        <div className="bg-gray-100 grow flex flex-col items-center justify-center px-8 md:px-20 py-16 text-start mx-auto">
            <div className="grid grid-cols-1 md:grid-cols-2 gap-6" >
                <figure className=" bg-white shadow-lg rounded-lg overflow-hidden p-8">
                    <img src={redsysImage} alt="" className="h-full w-full" />
                </figure>
                <form className="flex flex-col justify-center" onSubmit={handleProcessPayment} ref={formRef}>
                    <div className="flex flex-col justify-around">
                        <div className="grow text-start">
                            <p className="flex items-center text-2xl text-blue-500 py-4"><span className="mr-3">Total a pagar: </span><span>{(typeof amount == 'undefined') ? <LoadingIcon width={20} height={20} /> : amount + "$ EUR"} </span></p>
                        </div>
                        <div className="grid gap-2">
                            <div className="col-span-2">
                                <TextFieldLabel
                                    title="N&uacute;mero de tarjeta"
                                    type="number"
                                    name="card_number"
                                    id="card_number"
                                    placeholder="N&uacute;mero"
                                    noVoid
                                    required
                                />
                            </div>
                            <div className="col-span-2 md:col-span-1">
                                <TextFieldLabel
                                    title="CVV"
                                    type="text"
                                    name="cvv"
                                    id="card_number"
                                    placeholder="cvv"
                                    noVoid
                                    required
                                />
                            </div>
                            <div className="col-span-2 md:col-span-1">
                                <TextFieldLabel
                                    title="Fecha de expiraci&oacute;n"
                                    type="date"
                                    name="expiration_date"
                                    id="expiration_date"
                                    placeholder="Fecha de expiraci&oacute;n"
                                    noVoid
                                    required
                                />
                            </div>

                        </div>
                        {amount ? <PrimaryButton text={'Pagar'} className="rounded-md h-fit md:w-fit mt-3" /> : null}
                    </div>
                </form>


            </div>
        </div>
        <Footer />
    </div>)
}

export default PayEnrollment;