import React from "react";
import NavBar from "../../components/NavBar/NavBar";
import Footer from "../../components/Footer/Footer";
import { Link, useNavigate, useParams } from "react-router-dom";
import successPayment from "../../assets/successPayment.svg"
import errorPayment from "../../assets/errorPayment.svg"
import PrimaryButton from "../../components/Buttons/PrimaryButton";

const CompletePayment = () => {

    const navigate = useNavigate();
    const { status } = useParams();


    return (
        <div className="h-screen flex flex-col">
            <NavBar />
            <figure className="bg-gray-100 grid grid-cols-1 py-16 px-5 text-center justify-center">
                <img src={status == 1 ? successPayment : errorPayment} alt="" className="h-20 w-full" />
                <figcaption className="mb-3 mt-2 text-3xl">
                    {status == 1 ? <>Su pago ha sido completado</> : <>Ha fallado la solicitud de pago</>}
                </figcaption>
                <figcaption className="my-2 text-sm">
                    {status == 1 ? <>Gracias por confiar en nosotros. Recibir&aacute;
                        un email con los detalles de la transacci&oacute;n realizada</> : <>Pulse el bot&oacute;n de abajo para volver a intentarlo</>}
                </figcaption>
                {status == 0 && <PrimaryButton
                    type="submit"
                    id="payNow"
                    className="rounded-full mb-2 px-4 w-full sm:w-64 m-auto"
                    text={'Volver a pagar'}
                    handleCLick={() => window.history.goBack()}
                />}
                <Link to={'/'} className="my-2 text-sm underline text-blue-500">
                    Volver al Home
                </Link>
            </figure>
            <Footer />
        </div>
    );
}



export default CompletePayment;