import NavBar from "../../components/NavBar/NavBar";
import Footer from "../../components/Footer/Footer";
import TitlePage from "../../components/TitlePage/TitlePage";
import TableColumn from "../../components/Table/TableColumn.js";
import TableRow from "../../components/Table/TableRow.js";
import Table from "../../components/Table/Table.js";
import ItemEdit from "../../components/Table/ItemEdit";
import ItemDelete from "../../components/Table/ItemDelete";
import AddButton from "../../components/Buttons/AddButton";
import SelectField from "../../components/SelectField/SelectField";
import Loading from "../../components/Loading/Loading";
import Alert from "../../components/PushMessage/Alert";
import { useState, useEffect } from "react";
import useUser from "../../hooks/auth/useUser";
import { getHistoricalStatistics } from "../../api/historicalStatistics";
import { getEditions } from "../../api/editions";

function HistoricalStatistics() {
  const [historical, setHistorical] = useState(false);
  const [editions, setEditions] = useState(false);
  const [stateAlert, setStateAlert] = useState(false);
  const [stateLoading, setStateLoading] = useState(false);
  const [userData, setUserData] = useUser();

  const Historical = (editionId) => {
    setStateAlert(false);
    setStateLoading(true);

    getHistoricalStatistics(editionId, (json, error) => {
      setStateLoading(false);

      if (json) {
        if (json.code == "ok") {
          setHistorical(json.data);
        } else {
          setStateAlert({
            type: "war",
            message: json.message,
          });
        }
      } else {
        setStateAlert({
          type: "err",
          message: "Ocurrio un error!, " + error,
        });
      }
    });
  };

  const Editions = () => {
    setStateAlert(false);
    setStateLoading(true);

    getEditions((json, error) => {
      setStateLoading(false);

      if (json) {
        if (json.code == "ok") {
          setEditions(json.data);
        } else {
          setStateAlert({
            type: "war",
            message: json.message,
          });
        }
      } else {
        setStateAlert({
          type: "err",
          message: "Ocurrio un error!, " + error,
        });
      }
    });
  };

  const onChangeEdition = (element) => {
    const value = element.target.value;
    if(value != "") Historical(value);
  };

  useEffect(() => {
    Editions();
  }, []);

  return (
    <>
      {stateLoading && <Loading />}
      {stateAlert && <Alert {...stateAlert} />}
      <NavBar />
      <div className="pl-2 pr-2 block h-full text-center bg-gray-100 pt-10 pb-20">
        <TitlePage text="Histórico y estadísticas" />
        <div className="pt-6 m-auto max-w-[300px]">
          <SelectField
            disabledTitle={true}
            onChange={onChangeEdition}
            default="Seleccione una liga"
            body={
              editions &&
              editions.map((item) => ({
                value: item.id,
                label: item.name,
              }))
            }
            required
            noVoid
          />
        </div>
        <div className="mt-4">
          dsd
        </div>
      </div>
      <Footer />
    </>
  );
}

export default HistoricalStatistics;
