import { Link, useNavigate, useParams } from "react-router-dom";
import { useState, useEffect } from "react";
import PrimaryButton from "../../components/Buttons/PrimaryButton.js";
import Loading from "../../components/Loading/Loading.js";
import Alert from "../../components/PushMessage/Alert.js";
import FormAccount from "../../components/Form/FormAccount.js";
import TextFieldLabel from "../../components/TextField/TextFieldLabel.js";
import CheckField from "../../components/CheckField/CheckField.js";
import { register } from "../../api/auth.js";
import useUser from "../../hooks/auth/useUser.js";
import useCookie from "../../hooks/auth/useCookie.js";

function Registro() {
  const [stateAlert, setStateAlert] = useState(false);
  const [stateLoading, setStateLoading] = useState(false);
  const [userData, setUserData] = useUser();
  const { referralCode } = useParams();
  const [cookie, setCookie] = useCookie('referralCode');
  const navigate = useNavigate();
  useEffect(() => {
    setStateAlert(false);
    if (!cookie || cookie == 'null' || cookie == 'undefined' && referralCode) {
      setCookie(referralCode)
    }
  }, []);

  const submit = (form) => {
    setStateAlert(false);
    form.preventDefault();
    //Recuperar data de el form
    const formValues = Object.fromEntries(new FormData(form.target).entries());
    const data = {
      name: formValues.name,
      lastName: formValues.lastName,
      password: formValues.password,
      phone: formValues.phone,
      email: formValues.email,
      referenceCode: cookie,
      acceptPP: formValues.acceptPP ? 1 : 0,
    };

    //Loading start
    setStateLoading(true);
    register(data, (json, error) => {
      //Loading stop
      setStateLoading(false);
      if (json) {
        if (json.code != "error") {
          //Register successfull
          setStateAlert({
            type: "ok",
            message: "Usuario registrado correctamente!",
          });
          //Guardando token
          setUserData({
            token: json.data.access_token,
            permissions: json.data.user.roles.includes("Admin")
              ? "admin"
              : "user",
          });
          //Ir al home
          navigate("/");
        } else {
          setStateAlert({
            type: "war",
            message: json.message,
          });
        }
      } else {
        //Error register
        setStateAlert({
          type: "err",
          message: "Ocurrio un error!, " + error,
        });
      }
    });
  };

  return (
    <div className="bg-gray-50">
      {stateAlert && <Alert {...stateAlert} />}
      {stateLoading && <Loading />}
      <FormAccount id="registro" title="Crear cuenta" submit={submit}>
        <div className="grid gap-2">
          <TextFieldLabel
            title="Nombre"
            type="text"
            name="name"
            id="name"
            placeholder="Tu nombre"
            noVoid
            required
          />
          <TextFieldLabel
            title="Apellidos"
            type="text"
            name="lastName"
            id="lastName"
            placeholder="Tus apellidos"
            noVoid
            required
          />
          <TextFieldLabel
            title="Correo electrónico"
            type="email"
            name="email"
            id="email"
            placeholder="ejemplo@correo.com"
            noVoid
            required
          />
          <TextFieldLabel
            title="Número de teléfono"
            type="tel"
            name="phone"
            id="phone"
            placeholder="Ejemplo 832 234 2424"
            noVoid
            required
          />
          <TextFieldLabel
            title="Contraseña para la cuenta"
            type="password"
            name="password"
            id="password"
            placeholder="No se permiten @!*.%"
            noVoid
            required
          />
        </div>
        <CheckField
          id="acceptPP"
          name="acceptPP"
          text={
            <>
              Acepto las politicas!,{" "}
              <Link
                to={"/policy"}
                className="font-medium text-primary-600 hover:underline dark:text-primary-500"
              >
                ver
              </Link>
            </>
          }
          required
        />
        <PrimaryButton
          type="submit"
          className="w-full focus:ring-4 focus:outline-none focus:ring-primary-300 font-medium rounded-lg text-sm px-5 py-2.5"
          text="Registrame"
        />
        <p className="text-sm font-light text-gray-500 dark:text-gray-400">
          Quiero iniciar a mi cuenta!{" "}
          <Link
            to={"/login"}
            className="font-medium text-primary-600 hover:underline dark:text-primary-500"
          >
            Iniciar
          </Link>
        </p>
      </FormAccount>
    </div>
  );
}

export default Registro;
